@import url(https://fonts.googleapis.com/css2?family=Patua+One&display=swap);
/* base styles */
body {
	font-family: 'Patua One', cursive;
	margin: 0;
	font-size: 1.5em;
	letter-spacing: 0.1em;
	text-align: center;
	background: #403b31;
	color: #fff;
}

.card {
	position: relative;
}
.card img {
	width: 100%;
	display: block;
	border: 2px solid #fff;
	border-radius: 6px;
}

/* front of card - the picture */
.card .front {
	-webkit-transform: rotateY(90deg);
	        transform: rotateY(90deg);
	transition: all ease-in 0.2s;
	position: absolute;
}
.flipped .front {
	-webkit-transform: rotateY(0deg);
	        transform: rotateY(0deg);
	transition-delay: 0.2s;
}

/* back of card - cover */
.card .back {
	transition: all ease-in 0.2s;
	transition-delay: 0.2s;
}
.flipped .back {
	-webkit-transform: rotateY(90deg);
	        transform: rotateY(90deg);
	transition-delay: 0s;
}

.App {
	max-width: 860px;
	margin: 40px auto;
}
button {
	background: none;
	border: 2px solid #fff;
	padding: 6px 12px;
	border-radius: 4px;
	font-family: 'Patua One', cursive;
	letter-spacing: 0.1em;
	font-size: 1em;
	color: #fff;
	cursor: pointer;
}
